* {
    margin: 0;
    padding: 0;

}
html {
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
    font-family: sans-serif;
}
.app-container {
    display: grid;
    place-items: center;
    grid-template-rows: 3fr max-content;
    gap: 48px;
    width: 100vw;
    height: 100vh;
}
.color-container {
    width: 60vw;
    height: 55vh;
    border-radius: 24px;
    color: #fff;
    display: grid;
    place-items: center;
    cursor: pointer;
}
.color-container div {
    display: grid;
    place-items: center;
    grid-template-rows: 5fr 3fr;
    gap: 12px;
}
h1 {
    background: #000;
    padding: 6px;
    border-radius: 12px;
}
p,h1 {
    cursor: default;
}

div button {
    width: fit-content;
    padding: 12px 24px;
    background: rgb(0, 132, 255);
    border: none;
    color: #fff;
    font-weight: 600;
    border-radius: 12px;
    cursor: pointer;
}

.copy-alert {
    position: absolute;
    width: 100%;
    top: 10vh;
    text-align: center;
    font-size: 24px;
}